@import url(https://fonts.googleapis.com/css?family=Rubik:400,500);
:root {
	/* Font family */
	--font-family_body: 'Rubik', helvetica, sans-serif;
	--font-family_heading: 'Rubik', helvetica, sans-serif;

	/* Font Sizes */
	--font-size_14: 0.875rem;
	--font-size_16: 1rem;
	--font-size_18: 1.125rem;
	--font-size_21: 1.3125rem;
	--font-size_24: 1.5rem;
	--font-size_28: 1.75rem;
	--font-size_36: 2.25rem;
	--body-font-size: var(--font-size_21);
	--body-line-height: 2rem;

	/* Font Weights */
	--font-weight_heading: 500;
	--font-weight_sub-heading: 500;
	--font-weight_body: 400;

	/* Colors */
	--color_tan-1: #f7f5f3;
	--color_tan-2: #eeece7;
	--color_tan-3: #e4e0d8;
	--color_tan-4: #d7d1c6;
	--color_white: #fff;
	--color_off-white: #f7f7f8;
	--color_light-gray: #e5e8eb;
	--color_medium-gray: #77838d;
	--color_dark-gray: #313c45;
	--color_black: #0e1216;

	/* UI Colors */
	--color_background: var(--color_tan-1);
	--color_primary: var(--color_dark-gray);
	--color_text: var(--color_black);

	/* Animation */
	--transition-duration-base: 150ms;
	--transition-duration-long: 350ms;
	--transition-timing-base: ease-in-out;

	/* Styles */
	--border-radius_btn: 0.25rem;
	--box-shadow_btn: 0 2px 8px 0 hsla(208, 22%, 7%, 0.2);
	--box-shadow_btn-hover: 0 2px 10px 2px rgba(17, 17, 18, 0.2);

	/* Breakpoints */
	--breakpoint_small: 37.5rem;
	--breakpoint_medium: 54rem;
	--breakpoint_large: 68rem;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	color: var(--color_text);
	font-family: var(--font-family_body);
	font-size: 100%;
	height: 100%;
	margin: 0;
}

canvas {
	vertical-align: top;
}

/* Document
 * ========================================================================== */

/**
 * 1. Correct the line height in all browsers.
 * 2. Prevent adjustments of font size after orientation changes in
 *    IE on Windows Phone and in iOS.
 */

html {
  line-height: 1.15; /* 1 */
  -ms-text-size-adjust: 100%; /* 2 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

/* Sections
 * ========================================================================== */

/**
 * Correct the font size and margin on `h1` elements within `section` and
 * `article` contexts in Chrome, Edge, Firefox, and Safari.
 */

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/* Grouping content
 * ========================================================================== */

/**
 * Remove the margin on nested lists in Chrome, Edge, IE, and Safari.
 */

dl dl,
dl ol,
dl ul,
ol dl,
ul dl {
  margin: 0;
}

/**
 * Remove the margin on nested lists in Edge 18- and IE.
 */

ol ol,
ol ul,
ul ol,
ul ul {
  margin: 0;
}

/**
 * 1. Add the correct box sizing in Firefox.
 * 2. Show the overflow in Edge 18- and IE.
 */

hr {
  box-sizing: content-box; /* 1 */
  height: 0; /* 1 */
  overflow: visible; /* 2 */
}

/**
 * Add the correct display in IE.
 */

main {
  display: block;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */

pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/* Text-level semantics
 * ========================================================================== */

/**
 * Add the correct text decoration in Edge 18-, IE, and Safari.
 */

abbr[title] {
  text-decoration: underline;
  text-decoration: underline dotted;
}

/**
 * Add the correct font weight in Chrome, Edge, and Safari.
 */

b,
strong {
  font-weight: bolder;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */

code,
kbd,
samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/**
 * Add the correct font size in all browsers.
 */

small {
  font-size: 80%;
}

/* Embedded content
 * ========================================================================== */

/**
 * Hide the overflow in IE.
 */

svg:not(:root) {
  overflow: hidden;
}

/* Forms
 * ========================================================================== */

/**
 * Remove the margin on controls in Safari.
 */

button,
input,
select {
  margin: 0;
}

/**
 * 1. Show the overflow in IE.
 * 2. Remove the inheritance of text transform in Edge 18-, Firefox, and IE.
 */

button {
  overflow: visible; /* 1 */
  text-transform: none; /* 2 */
}

/**
 * Correct the inability to style buttons in iOS and Safari.
 */

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

/**
 * Correct the padding in Firefox.
 */

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

/**
 * Show the overflow in Edge 18- and IE.
 */

input {
  overflow: visible;
}

/**
 * 1. Correct the text wrapping in Edge 18- and IE.
 * 2. Correct the color inheritance from `fieldset` elements in IE.
 */

legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-width: 100%; /* 1 */
  white-space: normal; /* 1 */
}

/**
 * 1. Add the correct display in Edge 18- and IE.
 * 2. Add the correct vertical alignment in Chrome, Edge, and Firefox.
 */

progress {
  display: inline-block; /* 1 */
  vertical-align: baseline; /* 2 */
}

/**
 * Remove the inheritance of text transform in Firefox.
 */

select {
  text-transform: none;
}

/**
 * 1. Remove the margin in Firefox and Safari.
 * 2. Remove the default vertical scrollbar in IE.
 */

textarea {
  margin: 0; /* 1 */
  overflow: auto; /* 2 */
}

/**
 * 1. Correct the odd appearance in Chrome, Edge, and Safari.
 * 2. Correct the outline style in Safari.
 */

[type="search"] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

/**
 * Correct the cursor style of increment and decrement buttons in Safari.
 */

::-webkit-inner-spin-button,
::-webkit-outer-spin-button {
  height: auto;
}

/**
 * Correct the text style of placeholders in Chrome, Edge, and Safari.
 */

::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.54;
}

/**
 * Remove the inner padding in Chrome, Edge, and Safari on macOS.
 */

::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * 1. Correct the inability to style upload buttons in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */

::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

/**
 * Remove the inner border and padding of focus outlines in Firefox.
 */

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
 * Restore the focus outline styles unset by the previous rule in Firefox.
 */

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
 * Remove the additional :invalid styles in Firefox.
 */

:-moz-ui-invalid {
  box-shadow: none;
}

/* Interactive
 * ========================================================================== */

/*
 * Add the correct display in Edge 18- and IE.
 */

details {
  display: block;
}

/*
 * Add the correct styles in Edge 18-, IE, and Safari.
 */

dialog {
  background-color: white;
  border: solid;
  color: black;
  display: block;
  height: -moz-fit-content;
  height: -webkit-fit-content;
  height: fit-content;
  left: 0;
  margin: auto;
  padding: 1em;
  position: absolute;
  right: 0;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
}

dialog:not([open]) {
  display: none;
}

/*
 * Add the correct display in all browsers.
 */

summary {
  display: list-item;
}

/* Scripting
 * ========================================================================== */

/**
 * Add the correct display in IE.
 */

template {
  display: none;
}

/* User interaction
 * ========================================================================== */

#root {
	background-color: var(--color_tan-1);
	height: 100%;
}

.app {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	height: 100%;
}

.main {
	-webkit-align-items: center;
	        align-items: center;
	cursor: pointer;
	display: -webkit-flex;
	display: flex;
	-webkit-flex: 1 1 auto;
	        flex: 1 1 auto;
	-webkit-justify-content: center;
	        justify-content: center;
}

.onboarding-panel {
	display: none;
	position: absolute;
}

.visible {
	display: block;
}

.content-block {
	max-width: 42rem;
	padding: 1rem;
	width: 100%;
}

p {
	font-size: var(--body-font-size);
	line-height: var(--body-line-height);
	margin: 0 0 2rem;
}

.center {
	text-align: center;
}

a {
	color: var(--color_text);
	-webkit-text-decoration: underline var(--color_primary);
	        text-decoration: underline var(--color_primary);
	transition: all var(--transition-duration-base)
		var(--transition-timing-base);
}

a:focus,
a:hover {
	opacity: 0.85;
	text-decoration: none;
}

.control-bar {
	border-top: 1px solid var(--color_tan-4);
	-webkit-flex: 0 0 auto;
	        flex: 0 0 auto;
	width: 100%;
}

.inner-container {
	-webkit-align-items: center;
	        align-items: center;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	width: 100%;
}

.controls-group {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
}

.controls-group-flex1 {
	-webkit-flex: 1 1;
	        flex: 1 1;
}

.container {
	padding: 0.5rem 1rem;
	width: 100%;
}

button {
	--_button-size-default: 3rem;
	--_button-size-large: 4rem;
	--_button-size-small: 2rem;

	-webkit-align-items: center;

	        align-items: center;
	background: var(--color_white);
	border: none;
	border-radius: var(--border-radius_btn);
	box-shadow: var(--box-shadow_btn);
	color: var(--color_black);
	cursor: pointer;
	display: -webkit-inline-flex;
	display: inline-flex;
	font-family: inherit;
	font-size: var(--font-size_16);
	height: var(--_button-size-default);
	-webkit-justify-content: center;
	        justify-content: center;
	line-height: 1;
	margin: 0 1rem 0 0;
	padding: 1rem;
	text-decoration: none;
	transition: all var(--transition-duration-base)
		var(--transition-timing-base);
}

button:active {
	background-color: var(--color_light-gray);
}

button:hover,
button:focus {
	background-color: var(--color_off-white);
	box-shadow: var(--box-shadow_btn-hover);
}

button:last-child {
	margin: 0;
}

button > * {
	pointer-events: none;
}

.large {
	height: var(--_button-size-large);
}

.small {
	height: var(--_button-size-small);
}

.primary {
	background-color: var(--color_primary);
	color: var(--color_white);
}

.primary:focus,
.primary:hover {
	background-color: var(--color_black);
	opacity: 0.9;
}

.round {
	border-radius: 100%;
	width: var(--_button-size-default);
}

.round-large {
	width: var(--_button-size-large);
}

.round-small {
	width: var(--_button-size-small);
}

.icon {
	display: inline-block;
	height: 1.5rem;
	margin-left: -4px;
	margin-right: 8px;
	overflow: visible;
	width: 1.5rem;
}

.icon-only {
	margin: 0;
}

.icon-large {
	height: 2rem;
	width: 2rem;
}

.slider-container {
	display: -webkit-flex;
	display: flex;
	margin: 0 1rem;
	max-width: 320px;
	width: 100%;
}

.label,
.readout {
	font-size: 1rem;
	line-height: 1;
	margin: 0;
}

input[type='range'] {
	--_slider_thumb-size: 2rem;
	--_slider-track-height: 0.5rem;
	--_slider_track-color: var(--color_tan-4);
	--_slider_width: 1.75rem;

	-webkit-appearance: none;
	background: transparent;
	margin: 0 0.75rem;
	width: 100%;
}

input[type='range']::-webkit-slider-thumb {
	-webkit-appearance: none;
	background: var(--color_white);
	border: none;
	border-radius: 100%;
	box-shadow: var(--box-shadow_btn);
	cursor: pointer;
	height: var(--_slider_thumb-size);

	/*
	Margin-top: thumb size - track size / 2 * -1
	 */
	margin-top: -0.75rem;
	-webkit-transition: all var(--transition-duration-base)
		var(--transition-timing-base);
	transition: all var(--transition-duration-base)
		var(--transition-timing-base);
	width: var(--_slider_thumb-size);
}

input[type='range']::-moz-range-thumb {
	background: var(--color_white);
	border: none;
	border-radius: 100%;
	box-shadow: var(--box-shadow_btn);
	cursor: pointer;
	height: var(--_slider_thumb-size);
	max-width: var(--_slider_width);
	-moz-transition: all var(--transition-duration-base)
		var(--transition-timing-base);
	transition: all var(--transition-duration-base)
		var(--transition-timing-base);
	width: 100%;
}

/* All the same stuff for IE */
input[type='range']::-ms-thumb {
	background: var(--color_white);
	border: none;
	border-radius: var(--border-radius_btn);
	box-shadow: var(--box-shadow_btn);
	cursor: pointer;
	height: var(--_slider_thumb-size);
	max-width: var(--_slider_width);
	-ms-transition: all var(--transition-duration-base)
		var(--transition-timing-base);
	transition: all var(--transition-duration-base)
		var(--transition-timing-base);
	width: 100%;
}

input[type='range']:hover {
	outline: none;
}

input[type='range']:focus::-webkit-slider-thumb,
input[type='range']:hover::-webkit-slider-thumb {
	background-color: var(--color_off-white);
	box-shadow: var(--box-shadow_btn-hover);
}

input[type='range']:focus::-moz-range-thumb,
input[type='range']:hover::-moz-range-thumb {
	background-color: var(--color_off-white);
	box-shadow: var(--box-shadow_btn-hover);
}

input[type='range']:focus::-ms-thumb,
input[type='range']:hover::-ms-thumb {
	background-color: var(--color_off-white);
	box-shadow: var(--box-shadow_btn-hover);
}

input[type='range']::-webkit-slider-runnable-track {
	background: var(--_slider_track-color);
	border: none;
	border-radius: var(--border-radius_btn);
	box-shadow: none;
	cursor: pointer;
	height: var(--_slider-track-height);
	width: 100%;
}

input[type='range']::-moz-range-track {
	background: var(--_slider_track-color);
	border: none;
	border-radius: var(--border-radius_btn);
	box-shadow: none;
	cursor: pointer;
	height: var(--_slider-track-height);
	width: 100%;
}

input[type='range']::-ms-track {
	background: var(--_slider_track-color);
	border: none;
	border-radius: var(--border-radius_btn);
	color: transparent;
	cursor: pointer;
	height: var(--_slider-track-height);
	width: 100%;
}

input[type='range']::-ms-fill-lower {
	background: var(--color_primary);
	border: none;
}

input[type='range']::-ms-fill-upper {
	background: var(--_slider_track-color);
	border: none;
}

.overlay {
	background-color: var(--color_background);
	display: -webkit-flex;
	display: flex;
	height: 100%;
	-webkit-justify-content: center;
	        justify-content: center;
	left: 0;
	overflow: auto;
	padding: 2rem;
	pointer-events: none;
	position: absolute;
	top: 0;
	transition-duration: var(--transition-duration-base);
	transition-property: opacity, visibility;
	transition-timing-function: var(--transition-timing-base);
	visibility: hidden;
	width: 100%;
	z-index: 100;
}

.visible {
	opacity: 0.95;
	pointer-events: auto;
	visibility: visible;
}

.close-btn {
	position: fixed;
	right: 1.5rem;
	top: 1.5rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: var(--font-family_heading);
	font-weight: var(--font-weight_heading);
}

h1 {
	font-size: 2.25rem;
	line-height: 3rem;
	margin: 0 0 3.5rem;
}

h2 {
	font-size: 1.75rem;
	line-height: 2.5rem;
	margin: 0 0 0.5rem;
}

